
html, body {
    margin: 0;
    height: 100%;
    background-color: #f5f5f5;
}

h1,h2{
    font-family: 'Poppins', sans-serif;
    color:#545454;
    margin: 8px 0px;
}

p,button,span{
    font-family: 'Open Sans', sans-serif;
    color:#686868;
    margin: 8px 0px;
}



/* body */
.container{
    position: relative;
}

.container-section{
    padding: 2% 10%;
}

.section-title{
    font-size: 36px;
}

.section-intro{
    position: relative;
    background-image: url(../images/background.jpg);
    background-size: cover;
    background-color: #3a9dd6;
    background-position: center;
    background-blend-mode: color-burn;
    height: 100vh;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container-text-intro h1{
    color: white;
    font-size: 24px;
    font-weight: 400;
    text-align: center;
    font-weight: 500;
    margin: 8px;
}

.container-text-intro h2{
    color: #f8c475;
    font-size: 30px;
    text-align: center;
    margin: 8px;
}

.container-text-intro p{
    color: white;
    font-size: 16px;
    text-align: center;
    margin: 8px;
    font-weight: 400;
}

.section-intro img{
    display: block;
    margin: 24px auto;
    width: 120px;
    height: 120px;
    object-fit:cover;
    border: 8px solid white;
    border-radius: 50%;
}

.container-btn {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: center;
}

.container-text-intro button{
    display: block;
    margin: 36px auto 0;
    padding: 16px 20px;
    border-radius: 144px;
    background-color:transparent;
    border: 2px solid white;
    color: white;
    font-size: 16px;
    font-weight: 700;
    text-align: center;
    outline:none;
}

.container-text-intro button:hover{
    background-color: white;
    color: #002663;
    cursor: pointer;
}

/*portfolio*/
.container-list-portfolio{
    margin: 24px 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
}
.portfolio-item{
    margin: 24px 24px 24px 0;
    padding: 20px;
    border-radius: 8px;
    width: 27%;
    background-color: #ffffff;
    cursor: pointer;
}

.portfolio-item:hover{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
}

.portfolio-item h2{
    font-size: 20px;
    font-weight: 600;
}

.portfolio-item a{
    text-decoration: none;
}

.portfolio-item img{
    width: 100%;
    height: 200px;
    object-fit:cover;
    border-radius: 4px;
}

.dribble-link{
    font-weight: 700;
    cursor: pointer;
    color: #3a9dd6;
}

.section-contact{
    margin: 2% 10%;
    border-radius: 32px;
    background-image: url(../images/background.jpg);
    background-color: #3a9dd6;
    background-position: center;
    background-blend-mode: color-burn;
    background-size: cover;
}

.section-contact h1{
    text-align: center;
    color: white;
}
.section-contact p{
    text-align: center;
    color: white;
}

.container-btn-contact{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 10%;
}

.container-btn-contact button{
    width: 150px;
    padding: 12px 0px;
    border-radius: 144px;
    margin: 12px 20px;
    font-size: 16px;
    color:white;
    background-color: transparent;
    border: 2px solid white;
    outline:none;
    cursor: pointer;
}

.container-btn-contact button:hover{
    color:#3a9dd6;
    background-color: white;
    color: #002663;
}

.container-btn-contact i{
    margin-right: 8px;
}

/* footer */
footer{
    background-color: #004a75;
    padding: 20px;
}

footer p{
    font-size: 16px;
    color: white;
    text-align: center;
}


/* mobile view */
@media only screen and (max-width: 480px) {
    .portfolio-item{
        width: 80%;
    }
  }

