.container-card-portfolio{
    margin: 4% 8%;
    border-radius: 16px;
    background-color: white;
    padding: 5%;
}


.container-card-portfolio hr{
    border: 1px solid #f5f5f5;
    margin: 20px 8px;
}

.container-card-portfolio h1{
    margin-top: 0;
    color: #1c7db6;
}

.section-paragraph{
    margin: 24px 0;
}


.section-paragraph h2{
    font-size: 24px;
}

.section-paragraph span{
    display: inline-block;
    background-color: #f5f5f5;
    padding: 8px 12px;
    border-radius: 4px;
    margin-top: 8px;
    margin-right: 12px;
}
.section-paragraph img{
    display: block;
    border-radius: 8px;
    width:80%;
    margin:5% auto;
    padding: 10px;
    border: 2px solid #f5f5f5;
}


.sticky-see-demo{
    display: flex;
    position: fixed;
    left: 0;
    bottom: 0;
    right: 0;
    padding: 12px;
    justify-content: space-around;
    align-items: center;
    background-color: white;
    box-shadow: 0 4px 15px 0 rgba(0, 0, 0, 0.1);
}

.demo-back {
    cursor: pointer;
    
}
.sticky-see-demo p{
    margin-right: 12px;
}

.sticky-see-demo button{
    padding: 8px 16px;
    background-color: #3a9dd6;
    border-radius: 8px;
    border: none;
    font-weight: 700;
    color: white;
    font-size: 16px;
    outline: none;
    cursor: pointer;
}

.sticky-see-demo button:hover{
    background-color: #1c7db6;
}

/* mobile view */
@media only screen and (max-width: 480px) {
    .demo-back {
        display: none;
    }
  }